.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.budget-container {
  font-family: Arial, sans-serif;
  margin: 0 auto;
  max-width: 600px;
  padding: 20px;
  background-color: #f2f2f2;
  border-radius: 10px;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.1);
}

.budget-info {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

.expense-form {
  margin-bottom: 20px;
}

.expense-form label {
  display: block;
  margin-bottom: 5px;
}

.expense-form input {
  width: 100%;
  padding: 8px;
  margin-bottom: 10px;
}

.expense-list {
  background-color: #fff;
  border-radius: 5px;
  padding: 10px;
}

.expense-item {
  margin-bottom: 10px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
  animation: fadeIn 0.5s ease-in-out;
}

.expense-text {
  background-color: #f9f9f9;
  padding: 8px;
  border-radius: 5px;
}

@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
